import "bootstrap/dist/css/bootstrap.min.css";
import "./assets/style.css";

import "bootstrap/dist/js/bootstrap.bundle";

import html2pdf from "html2pdf.js/dist/html2pdf";

import { useEffect, useRef, useState } from "react";

function App() {
  const contentRef = useRef();
  const inputRef = useRef();
  const [char, setChar] = useState(0);
  const [word, setWord] = useState(0);

  useEffect(() => {
    contentRef.current.focus();
  }, []);
  const handleNew = () => {
    contentRef.current.innerText = "";
    contentRef.current.focus();
    setChar(0);
    setWord(0);
  };

  const handleBold = () => {
    document.execCommand("bold");
  };

  const handleItalic = () => {
    document.execCommand("italic");
  };

  const handleUnderline = () => {
    document.execCommand("underline");
  };

  const handleChangeColor = (e) => {
    document.execCommand("forecolor", false, e.target.value);
  };

  const handleSaveTxt = (e) => {
    const a = document.createElement("a");
    const blob = new Blob([contentRef.current.innerText]);
    const dataUrl = URL.createObjectURL(blob);
    a.href = dataUrl;
    a.download = inputRef.current.value + ".txt";
    a.click();
  };

  const handleSavePdf = () => {
    html2pdf().from(contentRef.current).save(inputRef.current.value);
  };

  const handleKeyup = (e) => {
    setChar(e.target.innerText.trim().length);
    setWord(e.target.innerText.trim().split(/\s+/).length);
    if (e.target.innerText.trim() === "") {
      setWord(0);
    }
  };

  const handlePaste = (e) => {
    e.preventDefault();
    const text = e.originalEvent || e.clipboardData.getData("text/plain");

    document.execCommand("insertHTML", false, text.trim());
  };

  return (
    <div className="container">
      <h4 id="title">Doc Editor</h4>
      <div className="controls">
        <div className="main-controls">
          <div className="dropdown">
            <button
              className="btn btn-primary dropdown-toggle"
              data-bs-toggle="dropdown"
            >
              File
            </button>
            <div className="dropdown-menu">
              <button
                id="new-btn"
                className="dropdown-item"
                onClick={handleNew}
              >
                New
              </button>
              <button
                id="txt-btn"
                className="dropdown-item"
                onClick={handleSaveTxt}
              >
                Save as TXT
              </button>
              <button
                id="pdf-btn"
                className="dropdown-item"
                onClick={handleSavePdf}
              >
                Save as PDF
              </button>
            </div>
          </div>
          <input
            id="filename-input"
            type="text"
            defaultValue="untitled"
            ref={inputRef}
          />
        </div>
        <div className="control-btn-container">
          <button
            id="bold-btn"
            className="btn btn-dark control-btn"
            onClick={handleBold}
          >
            <b>b</b>
          </button>
          <button
            id="underline-btn"
            className="btn btn-dark control-btn"
            onClick={handleUnderline}
          >
            <u>u</u>
          </button>
          <button
            id="italic-btn"
            className="btn btn-dark control-btn"
            onClick={handleItalic}
          >
            <i>i</i>
          </button>
          <input
            id="color-btn"
            type="color"
            className="control-btn"
            onInput={handleChangeColor}
          />
        </div>
      </div>
      <div
        id="content"
        contentEditable="true"
        spellCheck="false"
        ref={contentRef}
        onKeyUp={handleKeyup}
        onPaste={handlePaste}
      />
      <div className="d-flex justify-content-end gap-3">
        <span>S k t: {char}</span>
        <span>S t: {word}</span>
      </div>
    </div>
  );
}

export default App;
